
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
  transition: all 0.4s linear;
  -moz-transition: all 0.4s linear /* Firefox 4 */;
  -webkit-transition: all 0.4s linear /* Safari 和 Chrome */;
  -o-transition: all 0.4s linear /* Opera */;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}
.cartcontrol .cart-decrease.move-transition {
  opacity: 1;
  transform: translate3D(0, 0, 0);
  -ms-transform: translate3D(0, 0, 0);
  -moz-transform: translate3D(0, 0, 0);
  -webkit-transform: translate3D(0, 0, 0);
  -o-transform: translate3D(0, 0, 0);
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave-active {
  opacity: 0;
  transform: translate3D(24px, 0, 0);
  -ms-transform: translate3D(24px, 0, 0);
  -moz-transform: translate3D(24px, 0, 0);
  -webkit-transform: translate3D(24px, 0, 0);
  -o-transform: translate3D(24px, 0, 0);
}
.cart-count {
  display: inline-block;
  vertical-align: top;
  padding-top: 6px;
  width: 12px;
  line-height: 24px;
  font-size: 10px;
  color: #93999f;
  text-align: center;
}
.cart-add {
  display: inline-block;
  padding: 6px;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}


