/*stylus方式定义了border-1px方法,然后通过mixin.styl媒体查询动态计算下边框宽度*/
.row {
  filter: blur(3px);
  z-index: 0;
}
.tab {
  display: flex /* 新版本语法: Opera 12.1, Firefox 22+ */;
  display: -ms-flexbox /* 混合版本语法: IE 10 */;
  display: -webkit-box /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.2);
  content: ' ';
}
.tab .tab-item {
  -ms-flex: 1 /* IE 10 */;
  flex: 1 /* NEW, Spec - Opera 12.1, Firefox 20+ */;
  -webkit-box-flex: 1 /* OLD - iOS 6-, Safari 3.1-6 */;
  -moz-box-flex: 1 /* OLD - Firefox 19- */;
  text-align: center;
/*选第一层a*/
}
.tab .tab-item > a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
.tab .tab-item > a.active {
  color: #f01414;
}


